.menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    margin: 0 auto;
}

.menu__item{
    color:#bebebe;
    text-shadow: 0px 0px 4px #369200;
    font-size: 1.25rem;
    font-weight: 600;
}

.menu__item:first-child{
    margin-right: 2rem;
}

.menu__item:last-child{
    margin-left: 2rem;
}

.menu__logo{
    margin: 0 2rem;
    height: 88px;
}